// 
import { REQUEST_STATE } from '../constants';

//  `foodsReducer` fetchState`INITIAL`foodsList
export const initialState = {
  fetchState: REQUEST_STATE.INITIAL,
  foodsList: [],
};

//  Action Type
export const foodsActionTypes = {
  FETCHING: 'FETCHING',
  FETCH_SUCCESS: 'FETCH_SUCCESS'
};

//  Reducer `state` 
export const foodsReducer = (state, action) => {
  switch (action.type) {
    case foodsActionTypes.FETCHING:  // fetchState`LOADING`
      return {
        ...state,
        fetchState: REQUEST_STATE.LOADING,
      };

    case foodsActionTypes.FETCH_SUCCESS:  //  fetchState`OK`  `foodsList` 
      return {
        fetchState: REQUEST_STATE.OK,
        foodsList: action.payload.foods,
      };

    default:
      throw new Error();  // 
  }
};
